.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.phonebook {
  margin: 50px;
}

h1,
h2 {
  color: #1e90ff;
}

button:hover,
button:focus {
  background-color: #1e90ff;
  border-radius: 3px;
  cursor: pointer;
}

input:hover {
  border-color: #1e90ff;
  border-radius: 3px;
}
